(function (global, factory) {
    typeof exports === 'object' && typeof module !== 'undefined' ? module.exports = factory() :
    typeof define === 'function' && define.amd ? define('bodyScroll', factory) :
    (global.bodyScroll = factory());
}(this, (function () { 'use strict';

    const html = document.documentElement;
    const head = document.head || document.getElementsByTagName('head')[0];

    const ua = navigator.userAgent;
    const isSafari = !!ua.match(/Version\/[\d\.]+.*Safari/);
    const isIOS = /iPad|iPhone|iPod/.test(ua) && !window.MSStream;

    const isSafariIOS = isIOS && isSafari;

    const BodyScrollEvent = (() => {
        if (typeof window.CustomEvent === 'function') {
            return window.CustomEvent;
        }

        function CustomEvent(event, params) {
            params = params || {
                bubbles: false,
                cancelable: false,
                detail: undefined
            };

            const evt = document.createEvent('CustomEvent');

            evt.initCustomEvent(
                event,
                params.bubbles,
                params.cancelable,
                params.detail
            );

            return evt;
        }

        CustomEvent.prototype = window.Event.prototype;

        return CustomEvent;
    })();

    let status = false;
    const setStatus = (bool = true) => (status = bool);

    let state = {
        scroll: {
            top: 0,
            left: 0,
        },
        html: {
            width: 'auto',
            height: 'auto'
        },
        body: {
            width: 'auto',
            height: 'auto',
            paddingRight: 0,
            paddingBottom: 0 // not useful // TODO: check
        },
        scrollbars: {
            y: 0,
            x: 0
        }
    };

    const setState = () => {
        const vw = window.innerWidth;
        const vh = window.innerHeight;

        let _state = {
            ...state,
            ...{
                scroll: {
                    top: html.scrollTop,
                    left: html.scrollLeft
                }
            }
        };

        if (isSafariIOS) {
            _state.html = {
                width: vw + _state.scroll.left, // - _state.scrollbars.y, // ios doesn't have scrollbars anyway...
                height: vh + _state.scroll.top // - _state.scrollbars.x
            };

            _state.body = {
                width: html.scrollWidth,
                height: html.scrollHeight
            };
        } else {
            const width = html.clientWidth;
            const height = html.clientHeight;

            _state.scrollbars = {
                y: vw > width ? vw - width : 0,
                x: vh > height ? vh - height : 0
            };

            _state.body.paddingRight = _state.scrollbars.y;
            // _state.body.paddingBottom = _state.scrollbars.x; // not useful // TODO: check
        }

        state = _state;
    };

    let settings = {
        important: true,
        corrections: []
    };

    const defaultCorrection = {
        selector: null,
        property: 'margin-right',
        inverted: false
    };

    const supportedProperties = [
        'margin-right',
        'margin-bottom',
        'padding-right',
        'padding-bottom',
        'right',
        'bottom'
    ];

    // TODO: put in setOptions
    const setCorrections = (collection = []) => {
        let corrections = [];

        const collectionType = typeof collection;
        if (
            collectionType === 'string' ||
            (!Array.isArray(collection) && collectionType === 'object')
        ) {
            collection = [collection];
        }

        collection.forEach(entry => {
            if (entry) {
                const entryType = typeof entry;

                if (entryType === 'string') {
                    corrections.push({
                        ...defaultCorrection,
                        ...{
                            selector: entry
                        }
                    });
                }

                if (
                    !Array.isArray(entry) &&
                    entryType === 'object' &&
                    entry.selector &&
                    (!entry.property ||
                        supportedProperties.indexOf(entry.property) > -1)
                ) {
                    corrections.push({
                        ...defaultCorrection,
                        ...entry
                    });
                }
            }
        });

        return corrections;
    };

    const setOptions = (options = {}) => {
        if (!status) {
            settings = {
                ...settings,
                ...options
            };
            settings.corrections = setCorrections(settings.corrections);
        }
    };

    const styler = id => {
        id = `body-scroll-${id}`;

        let element = head.querySelector(`style#${id}`);

        if (!element) {
            element = document.createElement('style');
            element.type = 'text/css';
            element.id = id;
            head.appendChild(element);
        }

        return element;
    };

    const updateStyle = () => {
        const important = settings.important ? '!important' : '';

        const $base = styler('base');
        $base.disabled = !status;
        if (!$base.sheet.cssRules.length) {
            // prettier-ignore
            $base.sheet.insertRule(`
            html,
            body {
                margin: 0${important};
                min-width: auto${important};
                min-height: auto${important};
                max-width: none${important};
                max-height: none${important};
            }
        `, 0);
            // prettier-ignore
            $base.sheet.insertRule(`
            html {
                padding: 0${important};
            }
        `, 1);
        }

        const $scrollbar = styler('scrollbar');
        $scrollbar.disabled = !status;
        if ($scrollbar.sheet.cssRules.length) {
            $scrollbar.sheet.deleteRule(1);
            $scrollbar.sheet.deleteRule(0);
        }
        // prettier-ignore
        $scrollbar.sheet.insertRule(`
        html {
            width: ${state.html.width}px${important};
            height: ${state.html.height}px${important};
        }
    `, 0);
        // prettier-ignore
        $scrollbar.sheet.insertRule(`
        body{
            width: ${state.body.width}px${important};
            height: ${state.body.height}px${important};
            padding: 0 ${state.body.paddingRight}px ${state.body.paddingBottom}px 0${important};
        }
    `, 1);

        const $lock = styler('lock');
        $lock.disabled = !status;
        if (isSafariIOS) {
            if (!$lock.sheet.cssRules.length) {
                // prettier-ignore
                $lock.sheet.insertRule(`
                html,
                body {
                    overflow: visible${important};
                }
            `, 0);
                // prettier-ignore
                $lock.sheet.insertRule(`
                html {
                    position: fixed${important};
                }
            `, 1);
            } else {
                $scrollbar.sheet.deleteRule(2);
            }

            // prettier-ignore
            $lock.sheet.insertRule(`
            html {
                top: ${state.scroll.top * -1}px${important};
                left: ${state.scroll.left * -1}px${important};
            }
        `, 2);
        } else {
            if (!$lock.sheet.cssRules.length) {
                // prettier-ignore
                $lock.sheet.insertRule(`
                body {
                    overflow: hidden${important};
                }
            `, 0);
            }
        }

        const corrections = settings.corrections;

        if (corrections.length) {
            const $corrections = styler('corrections');

            // prettier-ignore
            for(let i = $corrections.sheet.cssRules.length - 1; i >= 0; i-- ){
                $corrections.sheet.deleteRule(i);
            }

            corrections.forEach(entry => {
                const gap =
                    state.scrollbars[
                        entry.property.indexOf('right') > -1 ? 'y' : 'x'
                    ];

                if (gap > 0) {
                    let factor = 1;

                    if (!status) {
                        factor = entry.inverted ? -1 : 0;
                    }

                    // prettier-ignore
                    $corrections.sheet.insertRule(`
                    ${entry.selector} {
                        ${entry.property}: ${gap * factor}px${settings.important ? '!important' : ''};
                    }
                `);
                }
            });
        }
    };

    const lock = () => {
        if (!status) {
            setStatus(true);

            setState();

            updateStyle();

            window.dispatchEvent(new BodyScrollEvent('bodyScrollLock'));
        }
    };

    const unlock = () => {
        if (status) {
            setStatus(false);

            updateStyle();

            html.scrollTop = state.scroll.top;
            html.scrollLeft = state.scroll.left;

            window.dispatchEvent(new BodyScrollEvent('bodyScrollUnlock'));
        }
    };

    const toggle = () => (!status ? lock() : unlock());

    let timer = null;

    window.addEventListener('resize', () => {
        clearTimeout(timer);
        
        timer = setTimeout(() => {
            if (status) {
                unlock();

                lock();
            }
        }, 500);
    });

    var bodyScroll = {
        lock: lock,
        unlock: unlock,
        toggle: toggle,
        isLocked: () => status,
        setOptions: setOptions
    };

    return bodyScroll;

})));
